import { Link } from "gatsby"
import Footer from '../components/footer'
import MyForm from './myform'
let pathPrefix = `/`
export default () => (

<div>
  <Helmet>
      <meta charSet="utf-8" />
      <title>Convy - 5 Pro Tips For Visual Hierarchy</title>
      <link rel="canonical" href="https://convy.io" />
    </Helmet>
    <div className="row expanded convy_hero_inner">
        <div className="small-3 columns">
            <div className="top-bar-title">
                <a href="https://www.convy.io" onclick="track('Click-logo-top','Click-logo-top')" rel="canonical">
                <img className="convy_logo_app" src={require('../images/convy_logo.svg')} />
                </a>
            </div>
        </div>
        <div className="small-9 columns">
            <div id="topbar-responsive" className="topbar-responsive-links">
                <div className="top-bar-right">
                    <ul className="menu_items">
                        <li>
                        <a href="https://convy.io/howconvyworks" onclick="track('Click-logo-top-hdiw','Click-logo-top-hdiw')" rel="canonical">How does it work?</a>
                        </li>
                        <li>
                        <a href="#convy_pricing" onclick="track('Click-logo-top-pricing','Click-logo-top-pricing')" rel="canonical">Pricing</a>
                        </li>
                        <li>
                        <a href="https://convy.io/read" onclick="track('Click-logo-top-blog','Click-logo-top-blog')" rel="canonical">Resources</a>
                        </li>
                        <li>
                        <a href="#convy_contact" onclick="track('Click-logo-top-contact','Click-logo-top-contact')" rel="canonical">Contact</a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

    {/* begin terms */}
    <div className="gradient-bg"> 
          <img className="hero-bg-article" src={require('../images/blog_hierarchy.svg')} />
          <div className="callout large primary">
            <div className="row column text-center">
              <h2>Convy's Pro Tips</h2>
              <h1>5 Pro Tips For Visual Hierarchy</h1>
            </div>
          </div> {/* callout large primary */}
        </div>{/* End gradient-bg */}
        <div className="row padding10_bottom padding5_top" id="content">
          <div className="small-11 medium-8 columns">
            <div className="blog-post">
              <p>A common error in web design is to make all the elements on a page competing among themselves. By adding visual weight to certain parts of your design, you increase the chance that a user will see them and follow the path you designed. In this article we will provide tips to organize the elements on your page according to your strategy and improve conversion. </p>
              <h3 className="padding5_top">1) Make A Hierarchy Of Actions And Goals</h3>
              <p>Our job is to guide users to the things that matter. The user and business goals should therefore be reflected in the visual hierarchy of your page. First step will therefore be to identify and prioritize all the goals of the page and their respective call to action.</p>
              <p>Call to action are usually buttons but might also be a form, a text link, the navbar... Most of the landing pages do not display any navbar in order to keep the attention focused on the main page goal. Overall, you want more important things to be "bigger" than less important things. Bigger in size or bigger in contrast. </p>
              <p>Once the main call to action is identified, change the visual weight of the secondary action. A greyed and underlined link is the most efficient way to avoid visual competition between secondary actions and primary call to action</p>
              <h3 className="padding5_top">2) Think Contrast First</h3>
              <p>If the page or app you developped followed the classic design process, wireframes should have been provided in the first steps. Wireframes rely on few colors/contrasts and basic design principles that make the visual hierarchy clear and powerful. Visual design and color addition can sometimes dilute the original visual weight and hierarchy.</p>
              <video autoPlay loop muted className="video_article">
                <source src={require('../images/blog_hierarchy_contrast.mp4')} type="video/mp4" />
              </video>
              <div className="image_caption">Good use of contrasts and white spaces
                <a className="caption_source" target="_blank" href="https://dribbble.com/shots/4241950-2thepoint-Ridesharing-App">source</a>
              </div>
              <p className="padding5_top">Contrast rules our perception, not colors. Various usability studies confirmed that the elements that convert best are meeting high contrast standards. Keep this in mind when designing the final design. Are contrasts still powerfull enough to clearly identify user and business goals ?</p>
              <p>We recommend designer to use WCAG 2.0 rules and a tool like Tanaguru to test their contracts. It will also be a good excercise to make sure your design is meeting accessibility requirements. </p>
              <h3 className="padding5_top">3) Make Good Use Of Typography</h3>
              <h4>Create typography hierarchy</h4>
              <p>Create typography hierarchy by using font size, font weight and colors (shades of the main color). Assign more size and contrast to the elements you priorized.</p>
              <h4>Let your texts breathe</h4>
              <p>Keep enough line and paragraph spacing to make your user feel comfortable when reading text in your page. Try to group text and keep them vertically aligned with enough spacing to help your user scan.</p>
              <h4>Keep your lines of text short</h4>
              <p>Reading long sentences is demanding and cumbersome... The optimal number for words in a line is between 9 to 13 words or 45 to 75 characters in a body paragraph, with a 66-character line (including spaces) widely regarded as the magic number for a line of text.</p>
              <h3 className="padding5_top">4) Leave Room For Blank, Negative Space Or Image</h3>
              <h4>White space balance</h4>
              <p className="padding5_bottom">Miles Davis and Picasso shared a reflection about negative space : they both thought that the most important elements in their creation were silence negative space. A musical melody is built with the spaces between musical notes. If you want your call to action to stand out, keep space around elements, don't give to much to look at.
                Maintaining a good amount of white space within the blocks of text can make a site more readable and easier to scan. Consider white space as an element as such. People's attention is constantly requested, they will be gratefull if you give them enough room to breath in. Remember contrast? Well, good news : white space is its best ally. Enough white space makes the colors more vivid, brighter and makes elements easier to be catch by the eye of the user. </p>
              <img src={require('../images/blog_hierarchy_whitespace.jpg')} />
              <div className="image_caption"><a className="caption_source" target="_blank" href="https://dribbble.com/shots/4241950-2thepoint-Ridesharing-App">source</a>
              </div>
              <p className="padding5_top">When done right it is a powerful (and invisible!) element of trust and persuasion, but make good use of it.
                This is not always easy to find the correct combination between elements and white spaces : too little white space makes it confusing and having too much space also impact connection between elements and design dynamic negatively. As always it's all about finding the correct balance.</p>
              <h4>Image</h4>
              <p>You can use images with the purpose to create space between elements but be cautious when using them : they create strong visual emphasis, capture attention and might unbalance your visual hierarchy. Keep also an eye on consistency by selecting images that showcase the purpose of your site and brand.</p>
              <h3 className="padding5_top">5) Priorize The Areas In Your Design Process</h3>
              <p>A good way to ensure that you respect the established user/business goals and inherent visual hierarchy is to priorize the development of the elements in the same order as the visual hierarchy. Think about what you want the user to look at first in your page, make a hierarchy of the elements and then design around that hierarchy. When working on secondary elements some new ideas and improvement might pop up and it will be easier to iterate.</p>
              <div className="callout">
                <ul className="menu simple">
                  <li><a href="https://robespra.github.io/uxpages/" target="_blank" rel="noopener">Author: espra_pages</a></li>
                  {/* <li><a href="#">Comments: 3</a></li> */}
                </ul>
              </div>
            </div>
          </div>
          <div className="small-11 medium-3 columns padding5_top" data-sticky-container>
            <div className="sticky" data-sticky data-anchor="content">
              <h4>About convy</h4>
              <p>convy is a Conversion Rate Optimization consulting company. We help your company optimize their digital revenue using proven marketing webdesign methodologies.</p>
              <a href="#convy_contact" className="button primary large" onclick="track('Click-article-visual-sidebar','Click-article-visual-sidebar')">Get started</a>
            </div>
          </div>
        </div>
        <div className="convy_testimonial">
          <div className="row expanded align-center">
            <div className="small-11 medium-7 columns">
              <div className="full-width-testimonial-icon ">
                <img className src={require('../images/testimonial_chat_leah.png')} alt="leah chat testimonial" />
              </div>
            </div>{/* small-11 medium-10 columns */}
          </div>{/* row */}
        </div>{/* convy_testimonial padding5_both */}
        <div id="convy_contact" className="convy_contact">
          <div className="index_twitter_bg_setup">
            <div className="row expanded align-center padding5_both">
              <div className="index_twitter_bg" />
              <h2 className="padding5_both convy_contact_h2">Get ready to convert more</h2>
              <div className="small-12 medium-11 columns card ">
                <MyForm/>
              </div>{/* column card */}
            </div>{/* row */}
          </div>{/* twitter_bg_setup */}
        </div>{/* convy_contact */}
    <div className="convy_footer">
        <Footer/>
    </div>
</div>
) 

